/* App.css */
body {
  background-color: #121212;
  color: white;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  transition: padding-left 0.3s ease-in-out;
}

.container.history-open {
  padding-left: 270px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  border-bottom: 1px solid #2a2a2a;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #5d5fef;
}

.nav-links {
  display: flex;
  gap: 20px;
}

.nav-links a {
  color: #888;
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.3s ease;
}

.nav-links a:hover, .nav-links a.active {
  color: white;
}

.get-started {
  background-color: #5d5fef;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.get-started:hover {
  background-color: #4a4cbe;
}

.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 60px;
}

.main-logo {
  font-size: 4rem;
  font-weight: bold;
  margin-bottom: 20px;
  color: #5d5fef;
}

.try-aperture {
  background-color: #2a2a2a;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  margin-bottom: 40px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.try-aperture:hover {
  background-color: #3a3a3a;
}

.search-bar {
  display: flex;
  width: 100%;
  max-width: 600px;
  margin-bottom: 40px;
}

.search-input {
  flex-grow: 1;
  padding: 12px;
  font-size: 1rem;
  background-color: #2a2a2a;
  border: none;
  border-radius: 4px 0 0 4px;
  color: white;
}

.search-button, .generate-button {
  padding: 12px 20px;
  font-size: 1rem;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s ease;
}

.search-button {
  background-color: #5d5fef;
  color: white;
  border-radius: 0 4px 4px 0;
}

.search-button:hover {
  background-color: #4a4cbe;
}

.generate-button {
  background-color: #2a2a2a;
  color: white;
  border-radius: 4px;
  margin-left: 10px;
}

.generate-button:hover {
  background-color: #3a3a3a;
}

.image-preview {
  width: 100%;
  max-width: 600px;
  height: 300px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 20px;
}

.slider {
  width: 100%;
  max-width: 600px;
  margin-bottom: 40px;
}

.slider input {
  width: 100%;
  -webkit-appearance: none;
  appearance: none;
  height: 4px;
  background: #2a2a2a;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
}

.slider input:hover {
  opacity: 1;
}

.slider input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  background: #5d5fef;
  cursor: pointer;
  border-radius: 50%;
}

.slider input::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: #5d5fef;
  cursor: pointer;
  border-radius: 50%;
}

.alert {
  width: 100%;
  max-width: 600px;
  padding: 12px;
  border-radius: 4px;
  margin-top: 20px;
}

.alert-error {
  background-color: #ff4444;
}

.alert-success {
  background-color: #00C851;
}

.history-section {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 250px;
  background-color: #1a1a1a;
  transition: transform 0.3s ease-in-out;
  z-index: 1000;
  overflow-y: auto;
}

.history-section.hidden {
  transform: translateX(-100%);
}

.history-section h2 {
  color: #888;
  font-size: 0.9rem;
  margin-bottom: 10px;
}

.history-section ul {
  list-style-type: none;
  padding: 0;
}

.history-section li {
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.history-section li:hover {
  background-color: #2a2a2a;
}

.history-section .close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  color: #888;
  cursor: pointer;
  font-size: 1.2rem;
}

.history-section .close-button:hover {
  color: white;
}

.footer {
  text-align: center;
  padding: 20px 0;
  color: #888;
  font-size: 0.9rem;
  margin-top: 40px;
}

@media (max-width: 768px) {
  .container {
    padding: 0 10px;
  }

  .main-logo {
    font-size: 3rem;
  }

  .search-bar {
    flex-direction: column;
  }

  .search-input, .search-button {
    width: 100%;
    border-radius: 4px;
    margin-bottom: 10px;
  }

  .history-section {
    width: 100%;
  }

  .container.history-open {
    padding-left: 0;
  }
}